import {Injectable} from '@angular/core';
import {Http} from '@angular/http';
import 'rxjs/Rx';


@Injectable()
export class ReveurService{
	http:any;
	baseUrl:String;

	constructor(http:Http){
		this.http =http;
		this.baseUrl = "http://localhost/hotels/reveur/wp-json/javaparagon/v1/";
	}

	test(){
		return this.http.get(this.baseUrl+'test').map(res => res.json());

	}
	checkAvailability(motelId,startDate,endDate){
		return this.http.get(this.baseUrl+'checkavailability',{params:{MotelId:motelId,from:startDate,to:endDate}}).map(res => res.json());
	}


}






